





















































import { Component, Vue } from 'vue-property-decorator';
import { Routes } from '@/router';
import { Icon } from '@/components';

interface INavLink {
  text: string;
  link: object;
  icon: string;
}

interface ISocialLink {
  label: string;
  link: string;
  icon: string;
}

@Component({
  components: {
    Icon,
  },
})
export default class TopAppBar extends Vue {
  private Routes: Routes = Routes;
  private isMobileNavExpanded: boolean = false;

  private readonly navLinks: INavLink[] = [
    {
      text: 'Home',
      link: { name: Routes.Home },
      icon: 'home',
    },
    {
      text: 'About',
      link: { name: Routes.About },
      icon: 'user',
    },
    {
      text: 'Projects',
      link: { name: Routes.Projects },
      icon: 'code',
    },
  ];

  private readonly socialLinks: ISocialLink[] = [
    {
      label: 'LinkedIn',
      link: 'https://www.linkedin.com/in/jack-domleo/',
      icon: 'linkedin',
    },
    {
      label: 'GitHub',
      link: 'https://github.com/JDomleo',
      icon: 'github',
    },
    {
      label: 'CodePen',
      link: 'https://codepen.io/JackDomleo/',
      icon: 'codepen',
    },
    {
      label: 'Twitter',
      link: 'https://twitter.com/jackdomleo7',
      icon: 'twitter',
    },
  ];

  private isCurrent(to: string): boolean {
    return this.$route.path === to;
  }
}
